
import Avatar from '@mui/material/Avatar';
import * as React from 'react';
import { useState } from 'react';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import * as yup from 'yup'
import { useFormik } from 'formik';
import img from '../images/img.jpg'
import axios from 'axios';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import { useNavigate } from 'react-router-dom';
import Loading from './loading';
const baseUrl = "https://masala90.herokuapp.com/api/user"

const validationSchema = yup.object({

 email: yup.string().email('Invalid Email').required('Email is missing'),

})


function Copyright(props) {
 return (
  <Typography variant="body2" color="text.secondary" align="center" {...props}>
   {'Copyright  '}
   <Link color="inherit" href="https://mui.com/">
    Your Website
   </Link>{' '}
   {new Date().getFullYear()}
   {'.'}
  </Typography>
 );
}

const theme = createTheme();

export default function ForgetPassword() {
 const [fail, setFail] = useState(false)
 const [fails, setFails] = useState(false)
 const [open, setOpen] = useState(false)
 const [success, setSuccess] = useState(false)
 const [loading, setLoading] = useState(false)
 const navigate = useNavigate()




 const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
 });


 const handleClick = () => {
  setOpen(true);
 };

 const handleClose = (event, reason) => {
  if (reason === 'clickaway') {
   return;
  }

  setOpen(false);
  setFail(false)
  setFails(false)
  setSuccess(false)
 };

 const handleSubmit = (event) => {
  event.preventDefault();
  const data = new FormData(event.currentTarget);
  console.log({
   email: data.get('email'),
   password: data.get('password'),
  });
 };
 const formik = useFormik({
  initialValues: {

   email: '',

  },
  validationSchema: validationSchema,
  onSubmit: async (values) => {
   setLoading(true)
   try {
    const { data } = await axios.post(`${baseUrl}/forget-password`, values)
    if (data.success) {
     setSuccess(true)

     return navigate('/')
     setLoading(false)

    }
   } catch (error) {
    if (error?.response?.data) {
     const { data } = error.response
     if (!data.success) return setFail(true)


     console.log(error.response.data)
     setLoading(false)

    }
   }
  },
 });


 return (<>
  <div style={{ zIndex: '-1', position: "fixed", width: '30%', margin: "0px", hieght: "30%", marginTop: "-200px" }}>
   <img src={img} alt={"yellow-background"} />
  </div>
  <Snackbar open={fail} autoHideDuration={6000} onClose={handleClose}>
   <Alert onClose={handleClose} severity="warning" sx={{ width: '100%' }}>
    An error occured! try again please!
   </Alert>
  </Snackbar>
  <Snackbar open={success} autoHideDuration={6000} onClose={handleClose}>
   <Alert onClose={handleClose} severity="success" sx={{ width: '100%' }}>
    Link has been sent to your email!
   </Alert>
  </Snackbar>

  <ThemeProvider theme={theme}>
   <Container component="main" maxWidth="xs">
    <CssBaseline />
    <Box
     sx={{
      marginTop: 8,
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
     }}
    >
     <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
      <LockOutlinedIcon />
     </Avatar>
     <Typography component="h1" variant="h5">
      ForgetPassword
     </Typography>
     {loading ? <Loading /> : <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>

      <TextField
       required
       fullWidth
       id="email"
       label="Email Address"
       name="email"
       autoComplete="email"

       value={formik.values.email}
       onChange={formik.handleChange}
       error={formik.touched.email && Boolean(formik.errors.email)}
       helperText={formik.touched.email && formik.errors.email}
      />
      <Button
       type="submit"
       fullWidth
       variant="contained"
       sx={{ mt: 3, mb: 2 }}
       onClick={formik.handleSubmit}
      >
       Get Link
      </Button>
      <Grid container>
       <Grid item xs>
        <Link href="/login" variant="body2">
         Login
        </Link>
       </Grid>
       <Grid item>
        <Link href="/signup" variant="body2">
         {"Don't have an account? Sign Up"}
        </Link>
       </Grid>
      </Grid>
     </Box>}
    </Box>
    <Copyright sx={{ mt: 8, mb: 4 }} />
   </Container>
  </ThemeProvider>
 </>
 );
}