import React from "react";
import {
 AppBar,
 Toolbar,
 CssBaseline,
 Typography,
 makeStyles,
 useTheme,
 useMediaQuery,
} from "@material-ui/core";
import { Link, Navigate, useNavigate } from "react-router-dom";
import DrawerComponent from "./Drawer";
import img from '../images/cropped-logo2.png'
import Button from '@mui/material/Button';



const useStyles = makeStyles((theme) => ({
 AppBar: {
  background: '#5BB318',
  position: 'fixed', top: "0", left: "0", right: "0"

 },
 navlinks: {
  marginLeft: theme.spacing(5),
  display: "flex",
 },
 logo: {
  flexGrow: "1",
  cursor: "pointer",
  "&:hover": {
   zIndex: "999",


  },

 },
 link: {
  textDecoration: "none",
  fontFamily: 'Alkalami',
  color: "white",
  fontSize: "20px",
  marginLeft: theme.spacing(4),
  "&:hover": {
   color: "yellow",
   transform: "scale(1.5)"

  },
 },
}));

function Navbars() {
 const classes = useStyles();
 const theme = useTheme();
 const isMobile = useMediaQuery(theme.breakpoints.down("md"));
 const navigate = useNavigate()

 const logoutButton = () => {
  localStorage.removeItem("user");
  return navigate('/login')
 }

 return (
  <>
   <AppBar position="static" component="nav" className={classes.AppBar} sx={{ background: "#9CFF2E", position: 'fixed', top: "0", left: "0", right: "0" }}>
    <CssBaseline />
    <Toolbar>
     <Typography variant="h4" className={classes.logo}>
      <img src={img} style={{ width: '50px' }} ></img>

     </Typography>
     {isMobile ? (
      <DrawerComponent />
     ) : (
      <div className={classes.navlinks}>
       <Link to="/" className={classes.link}>
        Home
       </Link>
       <Link to="/stores" className={classes.link}>
        Stores
       </Link>
       <Link to="/orders" className={classes.link}>
        Orders
       </Link>
       <Button variant="outlined" onClick={logoutButton} className={classes.link} >Log out</Button>

      </div>
     )}
    </Toolbar>
   </AppBar>
  </>);
}
export default Navbars;