import * as React from 'react';
import { useState } from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { Formik } from "formik"
import * as yup from 'yup'
import { useFormik } from 'formik';
import img from '../images/img.jpg'
import axios from 'axios';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import { useNavigation, useLocation, Navigate, useNavigate } from 'react-router-dom';
import Loading from './loading';


const baseUrl = "https://masala90.herokuapp.com/api/user"


const validationSchema = yup.object({
 name: yup.string().trim().required('Name is missing!'),
 email: yup.string().email('Invalid Email').required('Email is missing'),
 password: yup.string().trim().min(8, "password is too short!")
})

function Copyright(props) {
 return (
  <Typography variant="body2" color="text.secondary" align="center" {...props}>
   {'Copyright  '}
   <Link color="inherit" href="https://mui.com/">
    Your Website
   </Link>{' '}
   {new Date().getFullYear()}
   {'.'}
  </Typography>
 );
}
const handleSignUp = (values, formikActions) => {
 console.log.log(values, formikActions)
}


const theme = createTheme();

export default function SignUp() {
 const [fail, setFail] = useState(false)
 const [fails, setFails] = useState(false)
 const [open, setOpen] = useState(false)
 const [success, setSuccess] = useState(false)
 const [loading, setLoading] = useState(false)

 const { state } = useLocation();

 const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
 });
 const navigate = useNavigate()


 const handleClick = () => {
  setOpen(true);
 };

 const handleClose = (event, reason) => {
  if (reason === 'clickaway') {
   return;
  }

  setOpen(false);
  setFail(false)
  setFails(false)
  setSuccess(false)
 };
 const formik = useFormik({
  initialValues: {
   name: '',
   email: '',
   password: '',
  },
  validationSchema: validationSchema,
  onSubmit: async (values) => {
   // alert(JSON.stringify(values, null, 2));
   setLoading(true)


   try {
    const { data } = await axios.post(`${baseUrl}/create`, values)
    if (data.success) {
     console.log(data.newUser._id)
     setSuccess(true)
     setLoading(false)


     return setTimeout(() => { navigate('/Verification', { state: { data: data.newUser._id } }) }, 2000)


     // navigation.dispatch(StackActions.replace('Verification'))

    }
   } catch (error) {
    if (error?.response?.data) {
     const { data } = error.response
     if (!data.success) return setFail(true)

     console.log(error.response.data)
     setLoading(false)

    }
   }
  },
 });
 const handleSubmit = (event) => {
  event.preventDefault();
  const data = new FormData(event.currentTarget);
  console.log({
   email: data.get('email'),
   password: data.get('password'),
  });
 };

 return (<>
  <div style={{ zIndex: '-1', position: "fixed", width: '30%', margin: "0px", hieght: "30%", marginTop: "-200px" }}>
   {/* <img src={img} alt={"yellow-background"} /> */}
  </div>
  <Snackbar open={fail} autoHideDuration={6000} onClose={handleClose}>
   <Alert onClose={handleClose} severity="warning" sx={{ width: '100%' }}>
    Account already exists!
   </Alert>
  </Snackbar>
  <Snackbar open={fails} autoHideDuration={6000} onClose={handleClose}>
   <Alert onClose={handleClose} severity="warning" sx={{ width: '100%' }}>
    Password does not match
   </Alert>
  </Snackbar>
  <Snackbar open={success} autoHideDuration={6000} onClose={handleClose}>
   <Alert onClose={handleClose} severity="success" sx={{ width: '100%' }}>
    Your account created succesfully!
   </Alert>
  </Snackbar>

  <ThemeProvider theme={theme}>
   <Container component="main" maxWidth="xs">
    <CssBaseline />
    <Box
     sx={{
      marginTop: 8,
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
     }}
    >
     <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
      <LockOutlinedIcon />
     </Avatar>
     <Typography component="h1" variant="h5">
      Sign up
     </Typography>
     {loading ? <Loading /> : <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}>
      <Grid container spacing={2}>
       <Grid item xs={12} >







        <TextField
         autoComplete="given-name"
         name="name"
         type="name"
         required
         fullWidth
         id="name"
         label="Name"
         autoFocus
         value={formik.values.name}
         onChange={formik.handleChange}
         error={formik.touched.name && Boolean(formik.errors.name)}
         helperText={formik.touched.name && formik.errors.name}
        />
       </Grid>

       <Grid item xs={12}>
        <TextField
         required
         fullWidth
         id="email"
         label="Email Address"
         name="email"
         autoComplete="email"

         value={formik.values.email}
         onChange={formik.handleChange}
         error={formik.touched.email && Boolean(formik.errors.email)}
         helperText={formik.touched.email && formik.errors.email}
        />
       </Grid>
       <Grid item xs={12}>
        <TextField
         required
         fullWidth
         name="password"
         label="Password"
         type="password"
         id="password"
         autoComplete="new-password"
         value={formik.values.password}
         onChange={formik.handleChange}
         error={formik.touched.password && Boolean(formik.errors.password)}
         helperText={formik.touched.password && formik.errors.password}
        />
       </Grid>
       <Button
        type="submit"
        fullWidth
        variant="contained"
        sx={{ mt: 3, mb: 2 }}
        onClick={formik.handleSubmit}
       >
        Sign Up
       </Button>






      </Grid>

      <Grid container justifyContent="flex-end">
       <Grid item>
        <Link href="/login" variant="body2">
         Already have an account? Sign in
        </Link>
       </Grid>
      </Grid>
     </Box>}
    </Box>
    {/* <Copyright sx={{ mt: 5 }} /> */}
   </Container>
  </ThemeProvider>
 </>
 );
} 