

import * as React from 'react';
import { useState, useEffect } from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import * as yup from 'yup'
import { useFormik } from 'formik';
import axios from 'axios';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import { Navigate, useNavigate, Route } from 'react-router-dom';
import Loading from './loading';
import { AsyncStorage } from 'reactjs-async-localstorage';
import Home from './Home.js'
const baseUrl = "https://masala90.herokuapp.com/api/user"





const validationSchema = yup.object({

 email: yup.string().email('Invalid Email').required('Email is missing'),
 password: yup.string().trim().min(8, "password is too short!")
})


const theme = createTheme();

export default function Login() {
 const navigate = useNavigate()

 const [fail, setFail] = useState(false)

 const [open, setOpen] = useState(false)
 const [success, setSuccess] = useState(false)
 const [loading, setLoading] = useState(false)
 const [next, setNext] = useState(false)
 const [load, setLoad] = useState(false)




 const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
 });


 const handleClick = () => {
  setOpen(true);
 };

 const handleClose = (event, reason) => {
  if (reason === 'clickaway') {
   return;
  }


  setOpen(false);
  setFail(false)

  setSuccess(false)
 };

 const formik = useFormik({
  initialValues: {

   email: '',
   password: '',
  },
  validationSchema: validationSchema,
  onSubmit: async (values) => {
   setLoading(true)


   await axios.post(`${baseUrl}/signin`, values).then(async (data) => {
    await AsyncStorage.setItem('user', JSON.stringify(data.data))

    window.location.reload()



    setSuccess(true)



    setLoading(false)

   }).catch((error) => {
    console.log(error)
   })


















  },
 });
 const handleSubmit = (event) => {
  event.preventDefault();
  const data = new FormData(event.currentTarget);
  console.log({
   email: data.get('email'),
   password: data.get('password'),
  });
 };

 return (<>


  {/* <div style={{ zIndex: '-1', position: "fixed", width: '30%', margin: "0px", hieght: "30%", marginTop: "-200px" }}>
   <img src={img} alt={"yellow-background"} />
  </div> */}
  <Snackbar open={fail} autoHideDuration={2000} onClose={handleClose}>
   <Alert onClose={handleClose} severity="warning" sx={{ width: '100%' }}>
    Login unsuccesfull
   </Alert>
  </Snackbar>
  <Snackbar open={success} autoHideDuration={2000} onClose={handleClose}>
   <Alert onClose={handleClose} severity="success" sx={{ width: '100%' }}>
    Login successfull
   </Alert>
  </Snackbar>

  <ThemeProvider theme={theme}>
   <Container component="main" maxWidth="xs">
    <CssBaseline />
    <Box
     sx={{
      marginTop: 8,
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
     }}
    >
     <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
      <LockOutlinedIcon />
     </Avatar>
     <Typography component="h1" variant="h5">
      Sign in
     </Typography>
     {loading ? <Loading /> : <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
      <TextField
       required
       fullWidth
       id="email"
       label="Email Address"
       name="email"
       autoComplete="email"

       value={formik.values.email}
       onChange={formik.handleChange}
       error={formik.touched.email && Boolean(formik.errors.email)}
       helperText={formik.touched.email && formik.errors.email}
      />
      <TextField
       required
       fullWidth
       name="password"
       label="Password"
       type="password"
       id="password"
       autoComplete="new-password"
       value={formik.values.password}
       onChange={formik.handleChange}
       error={formik.touched.password && Boolean(formik.errors.password)}
       helperText={formik.touched.password && formik.errors.password}
      />

      <Button
       type="submit"
       fullWidth
       variant="contained"
       sx={{ mt: 3, mb: 2 }}
       onClick={formik.handleSubmit}
      >
       Sign In
      </Button>
      <Grid container>
       <Grid item xs>
        <Link href="/forgetpassword" variant="body2">
         Forgot password?
        </Link>
       </Grid>
       <Grid item>
        <Link href="/signup" variant="body2">
         {"Don't have an account? Sign Up"}
        </Link>
       </Grid>
      </Grid>
     </Box>}
    </Box>
    {/* <Copyright sx={{ mt: 8, mb: 4 }} /> */}
   </Container>
  </ThemeProvider>

 </>
 );
}