import * as React from 'react';
import Box from '@mui/material/Box';
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import RestoreIcon from '@mui/icons-material/Restore';
import FavoriteIcon from '@mui/icons-material/Favorite';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import Paper from '@mui/material/Paper';
import { Link, Navigate, useNavigate } from "react-router-dom";
import AddIcon from '@mui/icons-material/Add';
import StorefrontIcon from '@mui/icons-material/Storefront';
import ReceiptLongIcon from '@mui/icons-material/ReceiptLong';
import LogoutIcon from '@mui/icons-material/Logout';

export default function Bottom() {
 const navigate = useNavigate()
 const [value, setValue] = React.useState(0);
 const logoutButton = () => {
  localStorage.removeItem("user");
  window.location.reload()
 }

 return (
  <Paper sx={{ position: 'fixed', bottom: 0, left: 0, right: 0 }} elevation={3}>
   <Box fullwidth >
    <BottomNavigation
     showLabels
     value={value}
     onChange={(event, newValue) => {
      setValue(newValue);
     }}
    >
     <BottomNavigationAction component={Link} to="/" label="Add Store" icon={<AddIcon />} />
     <BottomNavigationAction component={Link} to="/stores" label="Stores" icon={<StorefrontIcon />} />
     <BottomNavigationAction component={Link} to="/orders" label="Order" icon={<ReceiptLongIcon />} />
     <BottomNavigationAction onClick={logoutButton} label="Logout" icon={<LogoutIcon />} />
    </BottomNavigation>
   </Box>
  </Paper >
 );
}